@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Playfair Display', serif;
  font-family: 'Poppins', sans-serif;
}

/* font-family: 'Playfair Display', serif; */
/* font-family: 'Poppins', sans-serif; */

a {
  text-decoration: none;
}

/* Header Css */
.navItem {
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  padding: 0px 60px;
  height: 40px;
  align-items: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.navItemEmail {
  display: flex;
  column-gap: 30px;
  font-size: 14px;
}

.navItemEmail svg:hover {
  color: #9400d3;
  cursor: pointer;
}

.navItemEmailIcons {
  display: flex;
  column-gap: 8px;
  align-items: center;
}

.navItemEmailIcons span:hover {
  color: #9400d3;
  cursor: pointer;
}

.navItemIcons {
  display: flex;
  column-gap: 15px;
  font-size: 14px;
  color: #000;
}

.navItemIcons svg:hover {
  color: #9400d3;
  cursor: pointer;
}

.contactBar {
  display: flex;
  align-items: center;
  column-gap: 65px;
}

.navNavbarItem {
  display: flex;
  height: 100px;
  background-color: #fff;
  align-items: center;
  padding: 0px 60px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  justify-content: space-between;
}

.iconBrand {
  display: flex;
  align-items: baseline;
  column-gap: 5px;
  cursor: pointer;
}

.iconBrand svg {
  font-size: 30px;
  color: #000;
}

.iconBrand span {
  font-size: 30px;
  color: #9400d3;
}

.brandMobile {
  display: none;
}

.contactIcon {
  display: flex;
  align-items: center;
  column-gap: 15px;
}

.contactIcon span {
  font-size: 13px;
}

.contactIcon h5 {
  font-weight: 600;
  font-size: 16px;
}

.contactIcon svg {
  font-size: 23px;
}

.cartNavbar {
  display: flex;
  column-gap: 20px;
  align-items: center;
}
.cartNavbarMobile {
  display: none;
}

.cartNavbar svg {
  font-size: 30px;
}

.iconBag svg {
  font-size: 24px;
}

.searchInput {
  display: flex;
  align-items: center;
}

.btnSearch {
  position: absolute;
  right: 20%;
}

.formWidth {
  background-color: #fff;
  width: 520px;
}

.navbar {
  font-family: 'Poppins', serif;
  font-size: 15px;
  line-height: 26px;
  text-transform: uppercase;
  display: flex;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #fff;
  height: 50px;
}

.nav-link {
  font-weight: 300;
}

.navbar-nav .nav-link.active,
.navbar-nav .show > .nav-link {
  color: #9400d3;
  font-weight: 600;
}

.offcanvas-body {
  padding: 0rem;
}

.offcanvas.show:not(.hiding),
.offcanvas.showing {
  display: none;
}

.iconMedsosOfcanvas {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

/* Home page Css */

.filter {
  background-color: #fff;
  width: 100%;
  border-radius: 20px;
  padding: 20px 0px;
}

.menuCategory {
  width: 300px;
  padding: 15px 15px;
  background-color: #fff;
  border-radius: 10px;
  height: max-content;
}

.dropdownCategory {
  border: 1px solid rgba(0, 0, 0, 0.3);
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 30px;
}

.dropdown-toggle .menuCategory h4 {
  font-size: 21px;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
}

.loadProduct button {
  background-color: #9400d3;
  color: #fff;
  font-weight: 500;
  width: 130px;
  height: 40px;
  border: none;
  border-radius: 5px;
}

.loadProduct button:hover {
  background-color: #620888;
  color: #fff;
  transition: all 0.4s ease-in-out;
}

.itemProduct {
  padding: 0px 45px;
  overflow-x: hidden;
}

.dropdown {
  text-align: center;
  font-size: 21px;
  font-weight: 600;
}
.dropdown .dropdown-toggle {
  font-weight: 500;
}

/* footer css */

.footer {
  background-color: #fff;
  box-shadow: 0.5rem 0 1rem rgba(0, 0, 0, 0.15) !important;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  padding: 30px 0px;
}

.footerContact {
  display: flex;
  align-items: center;
  column-gap: 5px;
  margin-bottom: 15px;
  color: rgba(0, 0, 0, 0.8);
}

.footerContact svg:hover {
  color: #9400d3;
  cursor: pointer;
}

.footer span:hover {
  color: #9400d3;
  cursor: pointer;
}

.footer p {
  color: rgba(0, 0, 0, 0.8);
}

.footeLink {
  list-style: none;
  padding: 0rem;
}

.footeLink li {
  margin-bottom: 15px;
  color: rgba(0, 0, 0, 0.8);
}

.footeLink li:hover {
  color: #9400d3;
  cursor: pointer;
}

.footerSosmed {
  display: flex;
  column-gap: 15px;
}

.footerSosmed svg {
  font-size: 22px;
  color: rgba(0, 0, 0, 0.8);
}

.footerSosmed svg:hover {
  color: #9400d3;
  cursor: pointer;
}

.footer h3 {
  font-weight: 600;
}

/* Login Css */

.loginItem {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 65vh;
}

.registerItem {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 105vh;
}

.loginCard {
  width: 500px;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15) !important;
  padding: 30px;
  text-align: center;
}

.loginCard h4 {
  font-size: 30px;
  font-weight: 600;
  letter-spacing: 1px;
}

.loginCard p {
  color: rgba(0, 0, 0, 0.5);
}

.loginForm input {
  height: 50px;
}

.btnForm {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btnForm button {
  background-color: #9400d3;
  color: #fff;
  font-weight: 500;
  width: 130px;
  height: 40px;
  border: none;
  border-radius: 5px;
}

.btnForm button:hover {
  background-color: #620888;
  color: #fff;
  transition: all 0.4s ease-in-out;
}

.btnForm span {
  font-weight: 500;
  letter-spacing: 1px;
  cursor: pointer;
}

/* detail pages css */

.detailBtn button {
  background-color: #9400d3;
  color: #fff;
  font-weight: 500;
  width: 130px;
  height: 40px;
  border: none;
  border-radius: 5px;
}

.detailBtn button:hover {
  background-color: #620888;
  color: #fff;
}

.detailBtn {
  display: flex;
  justify-content: space-between;
}

/* category css */

.categoryItem {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1.5px solid rgba(0, 0, 0, 0.1);
  font-size: 30px;
}

.categoryItem:hover {
  background-color: rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}

/* cart css */

.cartBorder {
  border: 1px solid rgba(0, 0, 0, 0.5);
  padding: 15px 15px;
  border-radius: 10px;
}

.iconRemove {
  position: absolute;
  margin: -26px;
  cursor: pointer;
}

.iconRemove svg {
  font-size: 30px;
}
.iconRemove svg:hover {
  color: #dc3545;
  transition: all 0.3s ease-in;
}

.btnRemove {
  display: flex;
  align-items: center;
}

.itemCart {
  display: flex;
  justify-content: space-between;
}

.itemCartImage img {
  width: 150px;
}

/* dashboard css */
.list-group-item.active {
  background-color: #9400d3;
}

/* Page Not found */

.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf h1 {
  font-size: 100px;
  font-weight: 700;
}

.pnf h2 {
  font-weight: normal;
}

.pnf-btn {
  color: #000000;
  border: 1px solid #000000;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}

.pnf-btn:hover {
  background-color: #000000;
  color: #fff;
  transition: all 0.3s ease-in;
}

/*filter category*/
.filterCategory {
  font-weight: 500;
  font-family: 'Poppins', sans-serif;
  display: flex;
  flex-direction: column;
}

.priceFilter {
  font-weight: 500;
  font-family: 'Poppins', sans-serif;
}

.cardProduct {
  display: flex;
}

@media only screen and (min-width: 1262px) {
  /* Home Page Css */
  .itemProduct {
    padding: 0px 20px;
  }
}

@media only screen and (max-width: 1120px) {
  /* Home Page Css */
  .itemProduct {
    padding: 0px 55px;
  }
}

@media only screen and (max-width: 1088px) {
  /* Home Page Css */
  .itemProduct {
    padding: 0px 65px;
  }
}
@media only screen and (max-width: 1001px) {
  /* Home Page Css */
  .itemProduct {
    padding: 0px 95px;
  }
}
@media only screen and (max-width: 1050px) {
  /* Home Page Css */
  .itemProduct {
    padding: 0px 75px;
  }
}

@media only screen and (max-width: 1052px) {
  /* Header Css */
  .formWidth {
    width: 400px;
  }
}

@media only screen and (max-width: 952px) {
  /* Header Css */
  .formWidth {
    width: 300px;
  }
}

@media only screen and (max-width: 992px) {
  /* Header Css */
  .navNavbarItem {
    display: none;
  }
  .navItem {
    padding: 0 10px;
  }
  .brandMobile {
    display: flex;
  }
  .cartNavbarMobile {
    display: flex;
  }
  .iconBrand span {
    font-size: 25px;
  }
  .iconBrand svg {
    font-size: 25px;
  }
  .offcanvas.show:not(.hiding),
  .offcanvas.showing {
    display: flex;
  }
  .navItemIcons {
    font-size: 18px;
    column-gap: 30px;
  }
  .navbar {
    height: 80px;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 999;
  }
  /* Home Page Css */
  .itemProduct {
    padding: 0px 15px;
  }
  .cartPage {
    margin-top: 100px;
    margin-bottom: 100px;
  }
  .productDetail {
    margin-top: 100px;
    margin-bottom: 100px;
  }
}

@media only screen and (max-width: 550px) {
  /* cart Css */
  .itemCart {
    flex-direction: column;
  }
}
@media only screen and (max-width: 520px) {
  /* Header Css */
  .navItem {
    display: none;
  }
}

@media only screen and (max-width: 400px) {
  .btnForm button {
    width: 100px;
  }
}
